<script setup>
  const date = new Date();
  const { data, error } = await useAsyncData("blog", async () => {
    const content = await queryContent("blog")
      .where({
        date: { $exists: true, $lte: date },
        draft: { $not: true },
        hidden: { $not: true },
        featured: { $exists: true, $eq: true }
      })
      .sort({ date: 1 })
      .limit(3)
      .find();

    return content;
  });

  if (error.value) {
    console.error(error.value);
  }
</script>
